import React, {useState} from 'react'

const CartItem = ({ product }) => {
    const styles = {
        image: {
            width: "100px"
        },
        text: {
            textAlign: "left"
        }
    }
    // const [items, setItems] = useState(JSON.parse(localStorage.getItem("items")));
    // const remove = (product) => {
    //     console.log(items)

    //     setItems(items.filter((e) => {
    //         return e !== product;
    //     }));

    //     localStorage.setItem(JSON.stringify('items',JSON.stringify(items)));
    // }
    return (
        <div>
            <table className="table" >
                <tr >
                    <td width="160px" style={styles.text}><img src={product.image} alt="Product Image" style={styles.image} /></td>
                    <td width="450px" style={styles.text}>{product.name}</td>
                    <td width="150px" style={styles.text}>₹{product.price}</td>
                    {/* <td style={styles.text}><button className="btn btn-sm btn-outline-danger" onClick={()=> {remove(product)}}>Remove</button></td> */}
                    <td  width="150px" style={styles.text}><button className="btn btn-sm btn-outline-danger" >Remove</button></td>
                </tr>

            </table>
        </div>
    )
}

export default CartItem
