import React from 'react'
import "./Main.css"

const Main = () => {
    const dynamicImage = () => {
        let imgs = document.querySelectorAll('.imgs');

        Array.from(imgs).forEach((img) => {
            img.addEventListener('click', () => {
                imgs.forEach((img) => {
                    img.style.width = "75px";
                })
                img.style.width = "76%";
            })
        })
    }
    return (
        <div className="main">
            <div className="imgs" id="expanded" onClick={dynamicImage}>
            </div>
            <div className="imgs" onClick={dynamicImage}>
            </div>
            <div className="imgs" onClick={dynamicImage}>
            </div>
            <div className="imgs" onClick={dynamicImage}>
            </div>
            <div className="imgs" onClick={dynamicImage}>
            </div>
        </div>
    )
}

export default Main
