        console.log(e.target.value);
        let items = everyItems[e.target.value];
        // Ensure item is not null or undefined before using it
        setItem(prevItems => {
            if (prevItems && Array.isArray(prevItems)) {
                return [...prevItems, items];
            } else {
                return [items];
            }
        });

