import React from 'react'
import {Link} from "react-router-dom";

const Header = () => {
    // styles for header
    let headerStyle = {
        position: "sticky",
        top: "0px",
        background: "white",
        maxWidth: "100vw",
        zIndex: "2",
        boxShadow: "0px 3px 8px grey"
    };
    return (
        <div style={headerStyle}>
            <nav className="navbar navbar-expand-lg bg-body-tertiary">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/" style={{fontSize: "22px"}}><b>Shoppers.com</b></Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Home</Link>
                            </li>
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link" to="/product">Products</Link>
                            </li>
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link" to='/cart'>Cart</Link>
                            </li>
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link" to='/dashboard'>Dashboard</Link>
                            </li>
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link" to='/login'>Login</Link>
                            </li>
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link" to='https://9e7f8e0f97e1d20d05.gradio.live'>ChatBot</Link>
                            </li>
                            <li style={{fontSize: "17px"}} className="nav-item">
                                <Link className="nav-link" to='/About'>About</Link>
                            </li>
                        </ul>
                        <form className="d-flex" role="search">
                            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                            <button className="btn btn-success" type="submit">Search</button>
                        </form>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Header
