import React, { useEffect, useState } from 'react'
import CartItem from './CartItem'
import { Link } from 'react-router-dom';

const Cart = () => {
    const [item, setItem] = useState([]);
    const styles = {
        height: {
            minHeight: "85vh",
            display: "flex",
            flexDirection: "row"
        },
        card: {
            width: "18rem",
            margin: "0px 90px",
            maxHeight: "340px"
        },
        link: {
            textDecoration: "none",
            color: "black",
            fontSize: "15px"
        },
        flexbox: {
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems:"top"
        }
    }
    let subtotal = 0;
    let deliveryFee = 299;
    for(let i=0 ; i<item.length ; i++){
        subtotal += parseFloat(item[i].price)
    }
    let taxes = parseInt(0.05 * subtotal);
    useEffect(() => {
        setItem(JSON.parse(localStorage.getItem("items")))
    }, [])
    return (
        <div className='container flex-row' style={styles.height}>
            <div >
                <h2 style={{ margin: "30px 0px" }}>Your Cart ({item.length} items)</h2>
                {localStorage.getItem("items") === null ? "No Items to display" :
                    item.map((product, index) => (
                        <>
                            <CartItem key={index} product={product} />
                            <hr />
                        </>
                    ))
                }
            </div>
            {subtotal === 0 ? "" :
                <div className="card my-3 py-3" style={styles.card}>
                    <div className="card-body" style={styles.flexbox}>
                        <h5 className="card-title" style={{height: "30px"}}>Order Summary</h5>
                        <table>
                            <tr style={{height: "30px"}}>
                                <td>Subtotal</td>
                                <td>₹{subtotal}</td>
                            </tr>
                            <tr style={{height: "25px"}}>
                                <td>Delivery</td>
                                <td>₹{subtotal > 999 ? deliveryFee=0 : deliveryFee}</td>
                            </tr>
                            <tr style={{height: "30px"}}>
                                <td>Gst (5%)</td>
                                <td>₹{taxes}</td>
                            </tr>
                            <tr style={{height: "60px"}}>
                                <td><b>Total</b></td>
                                <td><b>₹{subtotal+deliveryFee+taxes}</b></td>
                            </tr>
                        </table>
                        <button className='btn btn-success my-2'>Checkout  <i class="fa-solid fa-arrow-right"></i></button>
                        <hr />
                        <Link to="/product" style={styles.link}>or Continue Shopping</Link>
                    </div>
                </div>
            }

        </div>
    )
}

export default Cart
