import React, { useEffect, useState } from 'react'
import { GoogleLogin } from '@react-oauth/google';
import { jwtDecode } from "jwt-decode";
export default function Log() {
    const [login, setLogin] = useState(false)
    const [userCredentials, setUserCredentials] = useState(null);
    useEffect(() => {
        const storedCredentials = localStorage.getItem('userCredentials');

        if (storedCredentials) {
            setLogin(true);
            const credentials = JSON.parse(storedCredentials);
            setUserCredentials(credentials);
        } else {
            setLogin(false);
        }
    }, [])

    const logout = (e) => {
        e.preventDefault();
        setLogin(false);
        localStorage.removeItem('userCredentials');
    }
    const styles = {
        container : {
            minHeight: "80vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
        },
        border : {
            border: "2px solid grey",
            padding: "40px",
            borderRadius: "20px"
        }
    }
    return (
        <div className='container my-3' style={styles.container}>
            <div className="w-full max-w-xs absolute left-1/2  translate-x-[-50%]" style={styles.border}>
                <form className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
                    <div className="mb-4">
                        <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
                            Username
                        </label>
                        <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Username" />
                    </div>
                    <div className="mb-6">
                        <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="password">
                            Password
                        </label>
                        <input className="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="Password" />
                        {/* <p className="text-red-500 text-xs italic">Please choose a password.</p> */}
                    </div>
                    <div className="flex items-center justify-between">
                        <button className="btn btn-primary mx-3" type="button">
                            Sign In
                        </button>
                        <a className="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-800" href="#">
                            Forgot Password?
                        </a>

                    </div>
                    <div className='mt-5 w-100% justify-center flex container '>
                        <GoogleLogin
                            onSuccess={credentialResponse => {
                                const decoded = jwtDecode(credentialResponse?.credential);
                                localStorage.setItem('userCredentials', JSON.stringify(decoded));
                                setLogin(true);

                            }}
                            onError={() => {
                                console.log('Login Failed');
                                setLogin(false);

                            }}

                        />
                    </div>
                    <div className='container w-100%  flex flex-column justify-center mt-5 align-center'>
                        {
                            login &&
                            <button className="bg-blue-500  hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline  " type="button" onClick={logout}>
                                Logout from Google Account
                            </button>

                        }
                    </div>
                </form>


            </div>
        </div>
    )
}