import React from 'react'

const Footer = () => {
    let footerStyle = {
        display: "flex",
        justifyContent: "space-around",
        maxWidth: "100vw",
        botton: "0px",
        height: "70px"
    }
    let i = {
        margin: "0px 20px",
        transform: "scale(1.3)",
        cursor: "pointer",
        // border: "1px solid grey",
        // padding: "8px",
        // borderRadius: "50%"
    }

    return (
        <footer className='bg-dark text-light py-3' style={footerStyle}>
            <p>CopyRight &copy; Shoppers.com - All Rights Reserved.</p>
            <p>
                <i style={i} class="fa-brands fa-twitter"></i>
                <i style={i} class="fa-brands fa-facebook"></i>
                <i style={i} class="fa-brands fa-instagram"></i>
            </p>
        </footer>
    )
}

export default Footer
